<template>
  <div id="fabric-works-card-component">
    <div class="top-container">
      <div class="title-container">
        <h3 class="title">{{ title }}</h3>
      </div>
      <div class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>

    <el-carousel
      height="350px"
      :autoplay="false"
      arrow="always"
      indicator-position="none"
    >
      <el-carousel-item
        v-for="(item, index) in _.splitEvery(4, data)"
        :key="index"
      >
        <div class="carouse-item">
          <div
            v-for="(_item, _index) in item"
            :key="_index"
            class="fabric-work-item"
          >
            <FabricWorkItemCard :data="_item"></FabricWorkItemCard>
          </div>
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, toRefs } from "vue";
import * as _ from "ramda";
import FabricWorkItemCard from "./FabricWorkItemCard.vue";
import { FabricWork } from "@/typings/fabric";

const Component = defineComponent({
  name: "FabricWorksCard",
  components: {
    FabricWorkItemCard
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Array as PropType<FabricWork[]>,
      default: () => []
    },
    size: {
      type: Number,
      default: () => 2
    }
  },
  setup(props, { emit }) {
    const { title, data } = toRefs(props);

    const onExpandClick = () => {
      emit("expand", {
        visible: true,
        title: title.value,
        list: data.value
      });
    };

    return {
      _,

      onExpandClick
    };
  }
});

export default Component;
</script>

