<template>
  <div id="similar-fabric-item-card-component">
    <div
      class="cover-image"
      :style="{
        backgroundImage: `url('${formatImageUrl(data.image_url + '@250-250')}')`
      }"
      @click="onImageClick"
    ></div>

    <div class="content-container">
      <h3 class="title">{{ data.name }}</h3>
      <p class="description">
        {{ data.real_composition }}
      </p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { formatImageUrl } from "@/utils/image";
import { FabricResult } from "@/typings/fabric";

const Component = defineComponent({
  name: "SimilarFabricItemCard",
  props: {
    data: {
      type: Object as PropType<FabricResult>,
      default: () => ({})
    },
  },
  setup(props) {
    const { data } = toRefs(props);

    const onImageClick = async () => {
      const url = `${process.env.VUE_APP_DOMAIN}/web/fabric/${data.value.id}`;
      window.open(url);
    };

    return {
      onImageClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

