<template>
  <div class="want-to-see-more-component">
    <div class="fixed-button">
      <h2>Want to see more?</h2>
      <el-button class="button" type="success" @click="onButtonClick">
        Contact
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'long-arrow-alt-right']"
        ></font-awesome-icon>
      </el-button>
    </div>

    <el-dialog custom-class="fixed-dialog" v-model="visible" width="600px">
      <h2>Contact Us</h2>
      <div>
        <div class="item">
          <p>Email</p>
          <el-input type="email" v-model="form.from_email"></el-input>
        </div>
        <div class="item">
          <p>Name</p>
          <el-input v-model="form.sender_name"></el-input>
        </div>
        <div class="item">
          <p>Brand</p>
          <el-input v-model="form.sender_company"></el-input>
        </div>
        <div class="item">
          <p>Your message to us</p>
          <el-input type="textarea" rows="8" v-model="form.message"></el-input>
        </div>
      </div>

      <div class="buttons-wrapper">
        <el-button class="button" type="info" @click="visible = false">
          Cancel
        </el-button>
        <el-button class="button" type="success" @click="onSubmit">
          Send
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { reactive, ref, toRefs } from "vue";
import { postContactUs } from "@/apis/member";
import { ContactUsParams } from "@/typings/member";
import { ElNotification } from "element-plus";
import { emailRule } from "@/utils";

const initMessage = (link: string) => {
  return (
    "I’m interested in this\n" +
    "Hello, I’m interested in this one: " +
    link +
    ".\n" +
    "Please reply me and give more information"
  );
};

export default {
  name: "WantToSeeMore",
  props: {
    id: {
      type: Number,
      default: -1
    },
    category: {
      type: String,
      default: "fabric"
    }
  },
  setup: function(props: any) {
    const { id, category } = toRefs(props);
    const visible = ref(false);
    const form = reactive<ContactUsParams>({
      obj_id: 0,
      obj_category: "",
      from_email: "",
      sender_name: "",
      sender_company: "",
      message: initMessage(location.href)
    });

    const onButtonClick = async () => {
      visible.value = true;
      form.obj_id = id.value;
      form.obj_category = category.value;
      form.from_email = "";
      form.sender_name = "";
      form.sender_company = "";
      form.message = initMessage(location.href);
    };

    const onSubmit = async () => {
      if (!form.from_email || !emailRule.test(form.from_email)) {
        ElNotification({
          title: "Error",
          type: "error",
          message: `Please enter a valid email address.`
        });
        return;
      }
      if (!form.sender_company) {
        ElNotification({
          title: "Error",
          type: "error",
          message: `Please select the brand. `
        });
        return;
      }
      try {
        await postContactUs(form);
        ElNotification({
          title: "Success",
          type: "success",
          message: `You has been contacted.`
        });
      } finally {
        visible.value = false;
      }
    };

    return {
      visible,
      form,

      onButtonClick,
      onSubmit
    };
  }
};
</script>

