<template>
  <div id="fabric-work-item-card-component" @click="onCardClick">
    <div
      class="cover-image"
      :style="{
        backgroundImage: `url('${formatImageUrl(data.OurImage)}')`
      }"
    ></div>

    <div class="content-container">
      <div class="content-wrapper">
        <div class="brande-container">
          <h3 class="brand">{{ data.Brand }}</h3>
          <h2 class="style-number">{{ data.StyleNumber }}</h2>
        </div>
        <el-tooltip :content="data.Name" placement="bottom" effect="light">
          <p class="name">{{ data.Name }}</p>
        </el-tooltip>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { formatImageUrl } from "@/utils/image";
import { FabricWork } from "@/typings/fabric";

const Component = defineComponent({
  name: "FabricWorkItemCard",
  props: {
    data: {
      type: Object as PropType<FabricWork>,
      default: () => ({})
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    const onCardClick = async () => {
      window.open(data.value.Url, "__blank");
    };

    return {
      onCardClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

