<template>
  <el-dialog v-model="visible" width="80%">
    <template #title>
      <div class="top-container">
        <div class="title-container">
          <h3 class="title">Similar Fabrics</h3>
          <div class="buttons-container">
            <el-tag
              v-for="tag in types"
              :key="tag.value"
              :type="tag.checked ? 'success' : 'info'"
              size="large"
              :closable="false"
              @click="onTagClick(tag.value)"
            >
              {{ tag.label }}
            </el-tag>
          </div>
        </div>
      </div>
    </template>

    <div>
      <el-row :gutter="15">
        <el-col
          class="col"
          v-for="(item, index) in data"
          :key="index"
          :span="6"
        >
          <SimilarFabricsItemCard :data="item"></SimilarFabricsItemCard>
        </el-col>
      </el-row>
    </div>
  </el-dialog>
</template>

<script lang="ts">
import { defineComponent, PropType, ref } from "vue";
import SimilarFabricsItemCard from "./SimilarFabricsItemCard.vue";
import { FabricResult } from "@/typings/fabric";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "SimilarFabricsDialog",
  components: {
    SimilarFabricsItemCard
  },
  props: {
    data: {
      type: Array as PropType<FabricResult[]>,
      default: () => []
    },
    types: {
      type: Array as PropType<OptionItem[]>,
      default: () => []
    }
  },
  setup(props, { emit }) {
    const visible = ref(false);

    const onTagClick = (value: string) => {
      emit("tag-click", value);
    };

    const open = () => {
      visible.value = true;
    };

    return {
      visible,

      open,

      onTagClick
    };
  }
});

export default Component;
</script>

