#fabric-create-project-dialog-component
  .inputs-container
    padding 0 10%
    .label
      text-align start
      font-size $font-size-large
      font-weight 500
      padding 0 8px
      margin-bottom 15px

    :deep() .el-col
      margin-bottom 30px

    :deep() .el-input
      width 100%

    :deep() .el-select
      width 100%
</style>
