<template>
  <div id="double-fabric-card-component">
    <div class="top-container">
      <div class="title-container">
        <h3 class="title">{{ title }}</h3>
        <div class="description">{{ description }}</div>
      </div>
      <div class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <el-row :gutter="40" class="cards-container">
      <el-col v-for="item in _.splitAt(size, data)[0]" :key="item.id" :span="24 / size">
        <div class="card-container">
          <FabricCard :data="item"></FabricCard>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, toRefs } from "vue";
import * as _ from "ramda";
import FabricCard from "@/components/FabricCard.vue";
import { FabricRelatesResponse } from "@/typings/fabric";

const Component = defineComponent({
  name: "DoubleFabricCard",
  components: {
    FabricCard
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    description: {
      type: String,
      default: ""
    },
    data: {
      type: Array as PropType<FabricRelatesResponse[]>,
      default: () => []
    },
    size: {
      type: Number,
      default: () => 2
    }
  },
  setup(props, { emit }) {
    const { title, description, data } = toRefs(props);

    const onExpandClick = () => {
      emit("expand", {
        visible: true,
        title: title.value,
        description: description.value,
        list: data.value
      });
    };

    return {
      _,

      onExpandClick
    };
  }
});

export default Component;
</script>

