<script lang="ts">
import {
  defineComponent,
  toRefs,
  ref,
  reactive,
  computed,
  watch,
  Ref,
  onMounted
} from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import * as _ from "ramda";

import { loading } from "../../../utils";
import { ElNotification } from "element-plus";
import AddToCollectionDialog from "../../../components/AddToCollectionDialog.vue";
import DoubleFabricCard from "../../../components/DoubleFabricCard.vue";
import FabricCard from "../../../components/FabricCard.vue";
import FabricImageWith3DMode from "../../../components/FabricImageWith3DMode.vue";
import WantToSeeMore from "../../../components/WantToSeeMore.vue";
import CreateProjectDialog from "../CreateProjectDialog.vue";
import SimilarFabricDialog from "./SimilarFabricsDialog.vue";
import FabricWorksCard from "./FabricWorksCard.vue";
import FabricWorkItemCard from "./FabricWorkItemCard.vue";
import SimilarFabricsCard from "./SimilarFabricsCard.vue";

import {
  GetFabric,
  GetFabricRelates,
  PostAddFabricToFavorite,
  SetFabricDetail,
  GetFabricRelatedCollections,
  GetFabricRelatedProject
} from "../../../typings/store";
import {
  FabricDetailResponse,
  FabricFileResponse,
  FabricRelatedCollectionResponse,
  FabricRelatesResponse,
  FabricResult
} from "../../../typings/fabric";

import { copyToClipboard } from "../../../utils/clipboard";
import { iCreateDialog } from "../../../typings/component";
import { CollectionAddFabric } from "../../../typings/collection";
import { Project } from "../../../typings/project";
import { getSimilarFabrics } from "@/apis/fabric";
import { OptionItem } from "@/typings/asideFilter";

type ExpandData = {
  visible: boolean;
  title: string;
  description: string;
  list: FabricRelatesResponse[];
};

const colors = ["#5470c6", "#91cc76", "#f8c858", "#e66", "#409eff"];
const getTagColor = (tag: string): string => {
  const adapter: Adapter<string> = {
    "In Progress": colors[0],
    "Completed on time": colors[4],
    "Completed but delayed": colors[1],
    Dropped: colors[3],
    Delayed: colors[2],
    default: ""
  };
  return adapter[tag] || adapter.default;
};

const Component = defineComponent({
  name: "FabricDetail",
  components: {
    CreateProjectDialog,
    AddToCollectionDialog,
    DoubleFabricCard,
    FabricCard,
    FabricImageWith3DMode,
    WantToSeeMore,
    FabricWorksCard,
    FabricWorkItemCard,
    SimilarFabricsCard,
    SimilarFabricDialog
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();
    const createProjectDialog: Ref<iCreateDialog | null> = ref(null);
    const addToCollectionDialog: Ref<iCreateDialog | null> = ref(null);
    const similarFabricDialog: Ref<iCreateDialog | null> = ref(null);
    const fabricWorksVisible = ref(false);
    const sfData = ref<FabricResult[]>([]);
    const sfTypeData = ref<OptionItem[]>([]);

    const statusValue = ref("");
    const selectedId = ref(-1);

    const { id } = toRefs(props);

    const dialog = reactive<ExpandData>({
      visible: false,
      title: "",
      description: "",
      list: []
    });
    const mainFile = ref("");

    const detail = computed<FabricDetailResponse>(
      () => store.state.fabric.detail
    );
    const relates = computed<FabricRelatesResponse[]>(
      () => store.state.fabric.relates
    );
    const relatedCollections = computed<FabricRelatedCollectionResponse[]>(
      () => store.state.fabric.relatedCollections
    );
    const relatedProjects = computed<Project[]>(
      () => store.state.fabric.relatedProjects
    );

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetFabric, id.value);
        await store.dispatch(GetFabricRelates, id.value);
        await store.dispatch(GetFabricRelatedCollections, id.value);
        await store.dispatch(GetFabricRelatedProject, id.value);
        const { data: sfResponse } = await getSimilarFabrics(
          parseInt(id.value, 10)
        );
        statusValue.value = detail.value.status;
        mainFile.value = detail.value.media.length
          ? detail.value.media[0].file
          : "";
        selectedId.value = detail.value.media.length
          ? detail.value.media[0].id
          : 0;
        sfData.value = sfResponse.similar_fabrics;
        sfTypeData.value = sfResponse.similar_fabrics_tag_types.map(
          (type: string): OptionItem => ({
            label: type,
            value: type,
            checked: false
          })
        );
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    watch(id, init);

    onMounted(init);

    const getSecondaryMedia = (detail: FabricDetailResponse) => {
      return detail.media.reduce(
        (result, item: FabricFileResponse, index: number) => {
          if (index % 3) {
            (result[result.length - 1] as FabricFileResponse[]).push(item);
          } else {
            (result as FabricFileResponse[][]).push([item]);
          }
          return result;
        },
        []
      );
    };

    const onCopyLinkClick = () => {
      const href = window.location.href;
      copyToClipboard(href);
      ElNotification({
        title: "Success",
        type: "success",
        message: `Your Copied Link ${href}`
      });
    };

    const onAddToFavoriteClick = async () => {
      const { status, message } = await store.dispatch(
        PostAddFabricToFavorite,
        id.value
      );
      if (status === "success") {
        store.commit(SetFabricDetail, {
          ...detail.value,
          is_favorite: message.includes("add")
        });
      } else {
        ElNotification({
          title: "Error",
          type: "error",
          message
        });
      }
    };

    const onEditClick = async () => {
      await router.push({ name: "FabricEdit", params: { id: id.value } });
    };

    const onCreateProjectClick = async () => {
      if (!createProjectDialog.value) return;
      createProjectDialog.value.open(detail.value);
    };

    const onCarouseItemClick = (item: FabricFileResponse) => {
      mainFile.value = item.file;
      selectedId.value = item.id;
    };

    const onDownloadClick = (url: string) => {
      window.open(url, "blank");
    };

    const onCollectionClick = async (id: number) => {
      await router.push({ name: "CollectionDetail", params: { id } });
    };

    const onProjectClick = async (id: number) => {
      await router.push({ name: "ProjectDetail", params: { id } });
    };

    const onPlusClick = () => {
      if (!addToCollectionDialog.value) return;
      const params: CollectionAddFabric = {
        id: parseInt(id.value, 10),
        price: 0
      };
      addToCollectionDialog.value.open(params);
    };

    const onSimilarFabricDiloagClick = () => {
      if (!similarFabricDialog.value) return;
      similarFabricDialog.value.open();
    };

    const onSimilarFabricTagClick = async (tag: string) => {
      sfTypeData.value = sfTypeData.value.map((it: OptionItem) => ({
        ...it,
        checked: it.value === tag ? !it.checked : it.checked
      }));
      const queryString = sfTypeData.value.reduce(
        (str: string, it: OptionItem) => {
          if (it.checked) {
            if (str) {
              return str + "," + it.value;
            }
            return it.value;
          }
          return str;
        },
        ""
      );
      const { data } = await getSimilarFabrics(parseInt(id.value, 10), {
        tag_type: queryString
      });
      sfData.value = data.similar_fabrics;
    };

    const onExpandClick = (data: ExpandData) => {
      dialog.visible = data.visible;
      dialog.title = data.title;
      dialog.description = data.description;
      dialog.list = data.list;
    };

    return {
      createProjectDialog,
      addToCollectionDialog,
      similarFabricDialog,

      dialog,
      detail,
      relates,
      relatedCollections,
      relatedProjects,
      sfData,
      sfTypeData,
      mainFile,
      statusValue,
      selectedId,
      fabricWorksVisible,

      onCopyLinkClick,
      onAddToFavoriteClick,
      onCreateProjectClick,
      onEditClick,
      onCarouseItemClick,
      onDownloadClick,
      onCollectionClick,
      onProjectClick,
      onPlusClick,
      onSimilarFabricTagClick,
      onSimilarFabricDiloagClick,
      onExpandClick,
      getSecondaryMedia,
      getTagColor,
      _
    };
  }
});

export default Component;
</script>

