<template>
  <div id="similar-fabric-card-component" ref="refComponent">
    <div class="top-container">
      <div class="title-container">
        <h3 class="title">Similar Fabrics</h3>
        <div class="buttons-container">
          <el-tag
            v-for="tag in types"
            :key="tag.value"
            :type="tag.checked ? 'success' : 'info'"
            size="large"
            :closable="false"
            @click="onTagClick(tag.value)"
          >
            {{ tag.label }}
          </el-tag>
        </div>
      </div>
      <div class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>

    <el-carousel
      height="350px"
      :autoplay="false"
      arrow="always"
      indicator-position="none"
    >
      <el-carousel-item
        v-for="(item, index) in _.splitEvery(4, data)"
        :key="index"
      >
        <div class="carouse-item">
          <div
            v-for="(_item, _index) in item"
            :key="_index"
            class="fabric-work-item"
          >
            <SimilarFabricsItemCard :data="_item"></SimilarFabricsItemCard>
          </div>
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, toRefs } from "vue";
import * as _ from "ramda";
import SimilarFabricsItemCard from "./SimilarFabricsItemCard.vue";
import { FabricResult } from "@/typings/fabric";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "SimilarFabricsCard",
  components: {
    SimilarFabricsItemCard
  },
  props: {
    data: {
      type: Array as PropType<FabricResult[]>,
      default: () => []
    },
    types: {
      type: Array as PropType<OptionItem[]>,
      default: () => []
    }
  },
  setup(props, { emit }) {
    const onTagClick = (value: string) => {
      emit("tag-click", value);
    };

    const onExpandClick = () => {
      emit("expand");
    };

    return {
      _,

      onTagClick,
      onExpandClick
    };
  }
});

export default Component;
</script>

